.header button:hover {
  color: black;
}


@media only screen and (min-width: 768px) {
  .image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: 0;
    border-bottom-left-radius: 1rem;
    border-bottom-right-radius: 1rem;
  }

  .image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 0.8;
  }
  .header {
    position: relative;
    height: 700px;
    background-color: #f7f7f7;
  }

  .nav {
    height: 9rem;
    border-radius: 2rem;
    width: 98%;
    margin-left: 1%;
  }

  .nav img {
    height: 150px;
    width: 250px;
    z-index: 1;
  }
  .unvisibleBox {
    height: 350px;
  }

}

@media only screen and (max-width: 768px) {
  .image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
    overflow: hidden;
    border-bottom-left-radius: .5rem;
    border-bottom-right-radius: .5rem;
  }

  .image img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
  }

  .header {
    position: relative;
    height: 14rem;
    background-color: #f7f7f7;
  }
  .nav {
    height: 5rem;
    border-radius: 1rem;
    width: 96%;
    margin-left: 2%;
  }

  .nav img {
    height: 100px;
    width: 150px;
    z-index: 1;
    margin-top: -.75rem;
    margin-left: -5%;
  }
  .unvisibleBox {
    height: 200px;
  }
}
