.link {
  cursor: pointer;
}

.link:hover {
  color: #4b778d;
  animation: hoverItems 0.2s forwards;
}

@media only screen and (min-width: 768px) {
  .footer {
    height: 20rem;
    background-color: white;
    margin-top: 4rem;
    margin-bottom: 6rem;
  }

  .contact {
    position: "absolute";
    margin-left: "6rem";
    opacity: ".8";
  }
}

@media only screen and (max-width: 768px) {
  .footer {
    height: 'auto';
    min-height: 23rem;
    background-color: white;
    margin-top: 1rem;
    margin-bottom: 1rem;
  }

  .contact {
    position: "absolute";
    margin-left: "6rem";
    opacity: ".8";
    display: "inline-block";
    left: "25%";
    transform: "translateX(-50%)";
  }
}

@keyframes hoverItems {
  0% {
    scale: 1;
  }

  25% {
    scale: 1.025;
  }

  50% {
    scale: 1.05;
  }

  75% {
    scale: 1.075;
  }

  100% {
    scale: 1.1;
  }
}
