.header button:hover {
  color: black;
}

@media only screen and (min-width: 768px) {
  .image {
    position: absolute;
    top: 0;
    left: 0;
    width: 98%;
    height: 100%;
    overflow: hidden;
    z-index: 0;
    background-color: #4B778D;
    margin-left: 1%;
    border-radius: 2rem;
    
  }

  /* .image img {
    margin-left: 1%;
    width: 98%;
    height: 100%;
    object-fit: cover;
    opacity: 1;
    border-radius: 2rem;
    
  } */
  .header {
    position: absolute;
    height: 9rem;
    background-color: transparent;
    width: 100%;
    border-radius: 2rem;
    margin-top: 20px;
  }

}

@media only screen and (max-width: 768px) {
  .image {
    position: absolute;
    top: 0;
    left: 0;
    width: 96%;
    margin-left: 2%;
    height: 100%;
    z-index: 0;
    overflow: hidden;
    border-radius: 1rem;
  }

  .image img {
    margin-left: 1%;
    width: 98%;
    height: 100%;
    object-fit: cover;
    opacity:1;
    border-radius: 1rem;
  }

  .header {
    position: relative;
    height: 5rem;
    background-color: 'transparent';
    margin-top: 20px;
  }
}

/* responsive */
